<template>
  <div>
    <el-form ref="data" :model="data" label-width="100px">
      <el-form-item label="目标日期">
        <dtpicker :data="data.dt" @val_change="change_dt"></dtpicker>
      </el-form-item>
      <el-form-item label="销售顾问">
        <sel_celler :data="data.celler_id" cate="展厅" @val_change="change_celler"></sel_celler>
      </el-form-item>
      <el-form-item label="单车产值">
        <el-input v-model.number="data.chanzhi"></el-input>
      </el-form-item>
      <el-form-item label="单车按揭款">
        <el-input v-model.number="data.anjie"></el-input>
      </el-form-item>
      <el-form-item label="交车目标">
        <el-input v-model.number="data.soldtag"></el-input>
      </el-form-item>
      <el-form-item label="按揭台数">
        <el-input v-model.number="data.anjietag"></el-input>
      </el-form-item>
      <el-form-item label="特殊车台数">
        <el-input v-model.number="data.specialtag"></el-input>
      </el-form-item>
      <el-form-item label="任务完成率%">
        <el-input v-model.number="data.tagrate"></el-input>
      </el-form-item>
      <el-form-item>
        <el-col span="12" offset="12">
          <el-button @click="savedata" type="primary" v-if="state" icon="el-icon-edit">保存数据</el-button>
        </el-col>
        <el-col span="12" offset="12">
          <el-button @click="updatedata" type="primary" v-if="!state" icon="el-icon-edit">更新数据</el-button>
        </el-col>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import sel_celler from "@/components/common/sel_celler";
import dtpicker from "@/components/common/datepicker";
import axios from "axios";

export default {
  components: { sel_celler, dtpicker },
  data() {
    return {
      data: {},
      state: 1
    };
  },
  mounted() {
    this.clearinfo();
  },
  methods: {
    change_dt(d) {
      this.data.dt = d;
    },
    change_celler(d) {
      this.data.celler_id = d;
    },
    clearinfo() {
      this.data = {
        dt: new Date(),
        chanzhi: 3000,
        anjie: 4000,
        soldtag: 0,
        anjietag: 0,
        specialtag: 0,
        tagrate: 50,
        celler_id: ""
      };
    },
    input_err() {
      let err = [];
      !/\d+/gi.test(this.data.chanzhi) ? err.push("产值金额不正确") : "";
      !/\d+/gi.test(this.data.anjie) ? err.push("按揭款金额不正确") : "";

      !/\d+/gi.test(this.data.soldtag) ? err.push("交车目标不正确") : "";
      !/\d+/gi.test(this.data.anjietag) ? err.push("按揭数量不正确") : "";

      this.data.celler_id.length == 0 ? err.push("销售顾问名称为空") : "";

      !/\d+/gi.test(this.data.specialtag)
        ? err.push("特殊车型数量不正确")
        : "";

      !/\d+/gi.test(this.data.tagrate) ? err.push("任务完成率不正确") : "";

      return err;
    },
    savedata() {
      if (this.input_err().length == 0) {
        this.$confirm("要添加任务目标吗？", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        }).then(() => {
          let info = this.data;

          (info.year = new Date(this.data.dt).getFullYear()),
            (info.month = new Date(this.data.dt).getMonth() + 1),
            axios({
              url: require("@/config").axios.addtag,
              method: "post",
              data: {
                token: sessionStorage.getItem("token"),
                info
              }
            }).then(re => {
              if (re.data.code == 200) {
                this.$message("任务目标已保存");
                this.clearinfo();
              } else {
                this.$message("任务目标重复，保存失败");
              }
            });
        });
      } else {
        this.$message(this.input_err().join("，"));
      }
    },
    updatedata() {}
  }
};
</script>

<style scoped>
ul li {
  padding: 8px 0;
}

div button {
  width: 100%;
}
</style>